* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: rgb(243, 240, 229);
}
.resorts {
  color: rgb(0, 0, 5);
  display: grid;
  text-align: center;
  margin: 5rem auto;
  max-width: 1150px;
  background: white;
}
.resort {
  border-radius: 3%;
  background: #47b1b4;
  max-width: 1178px;
  width: 250px;
  margin: 15px;
}
.image {
  margin: 5px;
  padding: 5px;
  height: auto;
  height: 150px;
  width: 200px;
}

@media screen and (min-width: 768px) {
  .resorts {
    grid-template-columns: repeat(4, 1fr);
  }
}

h6 {
  margin: 5px;
  padding: 5px;
  font-size: small;
}

.App h1 {
  text-align: center;
  margin-top: 50px;
}

.compareResort {
  margin-left: 70px;
  line-height: 25px;
}

.snow {
  margin-left: 30px;
}
